@import url("https://fonts.googleapis.com/css2?family=Bodoni+Moda:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&family=Cormorant+Garamond:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");
html {
  font-family: "Cormorant Garamond", serif; }

h1,
h2,
h3,
h4,
h5,
h6,
.h1-like,
.h2-like,
.h3-like,
.h4-like,
.h5-like,
.h6-like {
  font-family: "Bodoni Moda", serif; }

h1 {
  font-size: 36px; }

h2 {
  font-size: 30px; }

h3 {
  font-size: 24px; }

h4 {
  font-size: 20px; }

h5 {
  font-size: 18px; }

h6 {
  font-size: 16px; }

.h1-like {
  font-size: 36px; }

.h2-like {
  font-size: 30px; }

.h3-like {
  font-size: 24px; }

.h4-like {
  font-size: 20px; }

.h5-like {
  font-size: 18px; }

.h6-like {
  font-size: 16px; }

h1 {
  line-height: 44px; }

h2 {
  line-height: 36px; }

h3 {
  line-height: 30px; }

h4 {
  line-height: 24px; }

h5 {
  line-height: 22px; }

h6 {
  line-height: 20px; }

.h1-like {
  line-height: 44px; }

.h2-like {
  line-height: 36px; }

.h3-like {
  line-height: 30px; }

.h4-like {
  line-height: 24px; }

.h5-like {
  line-height: 20px; }

.h6-like {
  line-height: 18px; }

h1 {
  font-weight: 600; }

h3 {
  font-weight: 600; }

h4 {
  font-weight: 600; }

h5 {
  font-weight: 600; }

h6,
.h6-like {
  font-weight: 400; }

p {
  font-style: normal;
  font-weight: normal;
  line-height: 20px;
  font-size: 14px; }

a,
button {
  font-style: normal;
  font-size: 13px;
  line-height: 16px; }

a {
  font-weight: 600; }

button {
  font-weight: 800; }

